// ==================== TYPES QUIZ ====================

/**
 * Niveaux de difficult pour les questions
 */
export type DifficulteQuestion = 'facile' | 'moyen' | 'difficile';

/**
 * Interface pour une question de quiz
 */
export interface Question {
  id: string;                     // ID unique
  question: string;               // Texte de la question
  options: string[];              // Liste des rponses possibles (4 options)
  reponseCorrecte: number;        // Index de la bonne rponse (0-3)
  explication?: string;           // Explication de la rponse (optionnel)
  difficulte: DifficulteQuestion; // Niveau de difficult
  points: number;                 // Points attribus pour cette question
}

/**
 * Interface pour un quiz complet
 */
export interface Quiz {
  id: string;                     // ID unique Firestore
  disciplineId: string;           // ID de la discipline
  titre: string;                  // Titre du quiz
  description?: string;           // Description (optionnel)
  questions: Question[];          // Liste des questions
  duree: number;                  // Dure en minutes
  isPremium: boolean;             // Quiz Premium uniquement
  noteMinimale: number;           // Note minimale pour russir (/20)
  createdAt: Date;                // Date de cration
  updatedAt?: Date;               // Dernire mise  jour
}

/**
 * Interface pour un rsultat de quiz
 */
export interface QuizResult {
  id: string;                     // ID unique
  quizId: string;                 // ID du quiz pass
  userId: string;                 // ID de l'lve
  score: number;                  // Score obtenu (/20)
  reponses: number[];             // Indices des rponses donnes
  tempsEcoule: number;            // Temps coul en minutes
  datePassage: Date;              // Date et heure de passage
  reussi: boolean;                // Quiz russi ou non
}

