/* 
   Progression d'un lve par discipline
   Stocke dans la collection Firestore "progressions"
   Document ID = `${userId}_${disciplineId}`
    */
export interface Progression {
  /** UID de l'lve (li  users/{uid}) */
  userId: string;

  /** ID de la discipline (li  disciplines/{id}) */
  disciplineId: string;

  /** Nom de la discipline (dnormalis pour l'affichage) */
  disciplineNom: string;

  /** IDs des ressources consultes par l'lve dans cette discipline */
  ressourcesVues: string[];

  /** IDs des quiz russis (score  seuil) dans cette discipline */
  quizReussis: string[];

  /** Nombre total de ressources dans la discipline (snapshot) */
  totalRessources: number;

  /** Nombre total de quiz dans la discipline (snapshot) */
  totalQuiz: number;

  /** Pourcentage d'avancement calcul (0100) */
  pourcentage: number;

  /** Horodatage du dernier accs  cette discipline */
  dernierAcces: any; // Timestamp Firestore

  /** Date de cration de la progression */
  createdAt: any;    // Timestamp Firestore

  /** Date de dernire mise  jour */
  updatedAt: any;    // Timestamp Firestore
}

/* 
   Badge de rcompense
   Stock dans la sous-collection "users/{uid}/badges/{badgeId}"
    */
export interface BadgeDefinition {
  /** Identifiant unique du badge (ex: "premier_pas") */
  id: string;

  /** Nom affich (ex: "Premier pas") */
  nom: string;

  /** Description courte */
  description: string;

  /** Emoji ou icne (ex: "") */
  icone: string;

  /** Texte de la condition  remplir */
  condition: string;

  /** Catgorie : ressources, quiz, discipline, streak */
  categorie: 'ressources' | 'quiz' | 'discipline' | 'streak' | 'performance';

  /** Le badge a-t-il t obtenu ? (calcul ct client) */
  obtenu: boolean;

  /** Date d'obtention (ISO string ou null) */
  dateObtenue?: string | null;
}

/* 
   Streak de connexion (srie de jours conscutifs)
   Stock dans "users/{uid}" comme champs additionnels
    */
export interface StreakData {
  /** Nombre actuel de jours conscutifs de connexion */
  streakActuel: number;

  /** Meilleure srie de connexion jamais atteinte */
  meilleurStreak: number;

  /** Date du dernier accs (format ISO YYYY-MM-DD) */
  dernierJourAcces: string;
}

/* 
   Rsum global de progression (agrgation)
   Utilis dans le Dashboard lve
    */
export interface ProgressionGlobale {
  /** Nombre total de ressources consultes (toutes disciplines) */
  totalRessourcesVues: number;

  /** Nombre total de quiz russis (toutes disciplines) */
  totalQuizReussis: number;

  /** Pourcentage moyen d'avancement (toutes disciplines) */
  pourcentageMoyen: number;

  /** Nombre de disciplines commences */
  disciplinesCommencees: number;

  /** Nombre de disciplines compltes  100% */
  disciplinesCompletees: number;

  /** Streak de connexion actuel */
  streakActuel: number;

  /** Meilleur streak de connexion */
  meilleurStreak: number;

  /** Liste des progressions par discipline */
  parDiscipline: Progression[];
}
