/**
 * ============================================================================
 * COMPOSANT SETTINGS ADMIN - PedaClic
 * ============================================================================
 * Page de configuration de la plateforme.
 * Permet de grer :
 *   - Informations de la plateforme
 *   - Tarifs Premium (mensuel / annuel)
 *   - Mode maintenance
 *   - Paramtres des quiz
 * 
 * Les paramtres sont stocks dans Firestore : collection "settings",
 * document "platform".
 * 
 * @author PedaClic Team
 * @version 1.0.0
 */

import React, { useState, useEffect, useCallback } from 'react';
import {
  doc,
  getDoc,
  setDoc,
  serverTimestamp,
} from 'firebase/firestore';
import { db } from '../../firebase';

// ==================== INTERFACES ====================

/** Configuration de la plateforme stocke dans Firestore */
interface PlatformSettings {
  /* Informations gnrales */
  siteName: string;
  siteDescription: string;
  contactEmail: string;
  contactPhone: string;

  /* Tarifs Premium (FCFA) */
  premiumMensuel: number;
  premiumAnnuel: number;

  /* Paramtres des quiz */
  quizDureeDefaut: number;         /* Dure par dfaut en minutes */
  quizNoteMinimale: number;        /* Note minimale pour russir (/20) */
  quizTentativesMax: number;       /* Nombre max de tentatives par quiz */

  /* Mode maintenance */
  maintenanceMode: boolean;
  maintenanceMessage: string;

  /* Mtadonnes */
  updatedAt?: any;
  updatedBy?: string;
}

/** Valeurs par dfaut */
const DEFAULT_SETTINGS: PlatformSettings = {
  siteName: 'PedaClic',
  siteDescription: "L'cole en un clic  Plateforme ducative sngalaise",
  contactEmail: 'contact@pedaclic.sn',
  contactPhone: '+221 XX XXX XX XX',
  premiumMensuel: 2000,
  premiumAnnuel: 20000,
  quizDureeDefaut: 15,
  quizNoteMinimale: 10,
  quizTentativesMax: 3,
  maintenanceMode: false,
  maintenanceMessage: 'PedaClic est en maintenance. Nous revenons bientt !',
};

// ==================== COMPOSANT PRINCIPAL ====================

const SettingsAdmin: React.FC = () => {
  // ==================== TAT ====================

  const [settings, setSettings] = useState<PlatformSettings>(DEFAULT_SETTINGS);
  const [originalSettings, setOriginalSettings] = useState<PlatformSettings>(DEFAULT_SETTINGS);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [successMessage, setSuccessMessage] = useState<string | null>(null);
  const [hasChanges, setHasChanges] = useState(false);

  // ==================== CHARGEMENT ====================

  /**
   * Charge les paramtres depuis Firestore
   * Si le document n'existe pas, on utilise les valeurs par dfaut
   */
  const loadSettings = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);

      const docRef = doc(db, 'settings', 'platform');
      const docSnap = await getDoc(docRef);

      if (docSnap.exists()) {
        const data = docSnap.data() as PlatformSettings;
        /* Fusionner avec les dfauts (au cas o de nouveaux champs sont ajouts) */
        const merged = { ...DEFAULT_SETTINGS, ...data };
        setSettings(merged);
        setOriginalSettings(merged);
      } else {
        /* Premier lancement : utiliser les valeurs par dfaut */
        setSettings(DEFAULT_SETTINGS);
        setOriginalSettings(DEFAULT_SETTINGS);
      }
    } catch (err) {
      console.error('Erreur chargement paramtres:', err);
      setError('Impossible de charger les paramtres.');
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    loadSettings();
  }, [loadSettings]);

  /* Auto-dismiss du message de succs */
  useEffect(() => {
    if (successMessage) {
      const timer = setTimeout(() => setSuccessMessage(null), 4000);
      return () => clearTimeout(timer);
    }
  }, [successMessage]);

  /* Dtecter les changements non sauvegards */
  useEffect(() => {
    const changed = JSON.stringify(settings) !== JSON.stringify(originalSettings);
    setHasChanges(changed);
  }, [settings, originalSettings]);

  // ==================== HANDLERS ====================

  /** Met  jour un champ dans les settings */
  const handleChange = (field: keyof PlatformSettings, value: any) => {
    setSettings((prev) => ({ ...prev, [field]: value }));
  };

  /** Sauvegarde les paramtres dans Firestore */
  const handleSave = async () => {
    try {
      setSaving(true);
      setError(null);

      /* Validation basique */
      if (settings.premiumMensuel < 0 || settings.premiumAnnuel < 0) {
        setError('Les tarifs ne peuvent pas tre ngatifs.');
        return;
      }

      if (settings.quizNoteMinimale < 0 || settings.quizNoteMinimale > 20) {
        setError('La note minimale doit tre entre 0 et 20.');
        return;
      }

      const docRef = doc(db, 'settings', 'platform');
      await setDoc(docRef, {
        ...settings,
        updatedAt: serverTimestamp(),
      });

      setOriginalSettings(settings);
      setHasChanges(false);
      setSuccessMessage('Paramtres sauvegards avec succs !');
    } catch (err) {
      console.error('Erreur sauvegarde paramtres:', err);
      setError('Impossible de sauvegarder les paramtres.');
    } finally {
      setSaving(false);
    }
  };

  /** Annuler les modifications (revenir aux valeurs d'origine) */
  const handleCancel = () => {
    setSettings(originalSettings);
    setHasChanges(false);
  };

  /** Rinitialiser aux valeurs par dfaut */
  const handleReset = () => {
    if (window.confirm('Rinitialiser tous les paramtres aux valeurs par dfaut ?')) {
      setSettings(DEFAULT_SETTINGS);
    }
  };

  // ==================== RENDU ====================

  if (loading) {
    return (
      <div className="admin-loading">
        <div className="admin-spinner" />
        <p>Chargement des paramtres...</p>
      </div>
    );
  }

  return (
    <div className="settings-admin">
      {/* ==================== EN-TTE ==================== */}
      <div className="admin-page-header">
        <div className="admin-page-header__info">
          <h1 className="admin-page-header__title"> Configuration</h1>
          <p className="admin-page-header__subtitle">
            Paramtres gnraux de la plateforme PedaClic
          </p>
        </div>
        <div className="admin-page-header__actions">
          {hasChanges && (
            <button className="admin-btn admin-btn-ghost" onClick={handleCancel}>
              Annuler
            </button>
          )}
          <button
            className="admin-btn admin-btn-primary"
            onClick={handleSave}
            disabled={!hasChanges || saving}
          >
            {saving ? ' Sauvegarde...' : ' Sauvegarder'}
          </button>
        </div>
      </div>

      {/* Bannire changements non sauvegards */}
      {hasChanges && (
        <div className="settings-unsaved-banner">
           Vous avez des modifications non sauvegardes.
        </div>
      )}

      {/* Messages d'erreur / succs */}
      {error && <div className="admin-alert admin-alert--error"> {error}</div>}
      {successMessage && <div className="admin-alert admin-alert--success"> {successMessage}</div>}

      {/* ==================== SECTION : INFORMATIONS GNRALES ==================== */}
      <div className="settings-section">
        <div className="settings-section__header">
          <h2 className="settings-section__title"> Informations gnrales</h2>
          <p className="settings-section__description">
            Informations de base de la plateforme
          </p>
        </div>

        <div className="settings-grid">
          {/* Nom du site */}
          <div className="settings-field">
            <label className="settings-label">Nom du site</label>
            <input
              type="text"
              className="admin-input"
              value={settings.siteName}
              onChange={(e) => handleChange('siteName', e.target.value)}
            />
          </div>

          {/* Description */}
          <div className="settings-field settings-field--full">
            <label className="settings-label">Description</label>
            <input
              type="text"
              className="admin-input"
              value={settings.siteDescription}
              onChange={(e) => handleChange('siteDescription', e.target.value)}
            />
          </div>

          {/* Email de contact */}
          <div className="settings-field">
            <label className="settings-label">Email de contact</label>
            <input
              type="email"
              className="admin-input"
              value={settings.contactEmail}
              onChange={(e) => handleChange('contactEmail', e.target.value)}
            />
          </div>

          {/* Tlphone */}
          <div className="settings-field">
            <label className="settings-label">Tlphone</label>
            <input
              type="tel"
              className="admin-input"
              value={settings.contactPhone}
              onChange={(e) => handleChange('contactPhone', e.target.value)}
            />
          </div>
        </div>
      </div>

      {/* ==================== SECTION : TARIFS PREMIUM ==================== */}
      <div className="settings-section">
        <div className="settings-section__header">
          <h2 className="settings-section__title"> Tarifs Premium</h2>
          <p className="settings-section__description">
            Montants des abonnements en FCFA
          </p>
        </div>

        <div className="settings-grid">
          {/* Mensuel */}
          <div className="settings-field">
            <label className="settings-label">Abonnement mensuel (FCFA)</label>
            <input
              type="number"
              className="admin-input"
              value={settings.premiumMensuel}
              onChange={(e) => handleChange('premiumMensuel', parseInt(e.target.value) || 0)}
              min={0}
              step={500}
            />
            <span className="settings-hint">Actuellement : {settings.premiumMensuel.toLocaleString('fr-FR')} FCFA/mois</span>
          </div>

          {/* Annuel */}
          <div className="settings-field">
            <label className="settings-label">Abonnement annuel (FCFA)</label>
            <input
              type="number"
              className="admin-input"
              value={settings.premiumAnnuel}
              onChange={(e) => handleChange('premiumAnnuel', parseInt(e.target.value) || 0)}
              min={0}
              step={1000}
            />
            <span className="settings-hint">
              conomie : {((settings.premiumMensuel * 12) - settings.premiumAnnuel).toLocaleString('fr-FR')} FCFA/an
            </span>
          </div>
        </div>
      </div>

      {/* ==================== SECTION : PARAMTRES DES QUIZ ==================== */}
      <div className="settings-section">
        <div className="settings-section__header">
          <h2 className="settings-section__title"> Paramtres des Quiz</h2>
          <p className="settings-section__description">
            Valeurs par dfaut pour la cration de quiz
          </p>
        </div>

        <div className="settings-grid">
          {/* Dure par dfaut */}
          <div className="settings-field">
            <label className="settings-label">Dure par dfaut (minutes)</label>
            <input
              type="number"
              className="admin-input"
              value={settings.quizDureeDefaut}
              onChange={(e) => handleChange('quizDureeDefaut', parseInt(e.target.value) || 0)}
              min={1}
              max={120}
            />
          </div>

          {/* Note minimale */}
          <div className="settings-field">
            <label className="settings-label">Note minimale pour russir (/20)</label>
            <input
              type="number"
              className="admin-input"
              value={settings.quizNoteMinimale}
              onChange={(e) => handleChange('quizNoteMinimale', parseInt(e.target.value) || 0)}
              min={0}
              max={20}
            />
          </div>

          {/* Tentatives max */}
          <div className="settings-field">
            <label className="settings-label">Tentatives max par quiz</label>
            <input
              type="number"
              className="admin-input"
              value={settings.quizTentativesMax}
              onChange={(e) => handleChange('quizTentativesMax', parseInt(e.target.value) || 0)}
              min={1}
              max={10}
            />
            <span className="settings-hint">0 = illimit</span>
          </div>
        </div>
      </div>

      {/* ==================== SECTION : MODE MAINTENANCE ==================== */}
      <div className="settings-section">
        <div className="settings-section__header">
          <h2 className="settings-section__title"> Mode Maintenance</h2>
          <p className="settings-section__description">
            Activer pour bloquer l'accs au site pendant les mises  jour
          </p>
        </div>

        <div className="settings-grid">
          {/* Toggle maintenance */}
          <div className="settings-field">
            <label className="settings-label">Mode maintenance</label>
            <div className="settings-toggle-wrapper">
              <button
                className={`settings-toggle ${settings.maintenanceMode ? 'settings-toggle--active' : ''}`}
                onClick={() => handleChange('maintenanceMode', !settings.maintenanceMode)}
              >
                <span className="settings-toggle__knob" />
              </button>
              <span className={`settings-toggle-label ${settings.maintenanceMode ? 'settings-toggle-label--active' : ''}`}>
                {settings.maintenanceMode ? ' Maintenance active' : ' Site en ligne'}
              </span>
            </div>
          </div>

          {/* Message de maintenance */}
          <div className="settings-field settings-field--full">
            <label className="settings-label">Message affich aux visiteurs</label>
            <textarea
              className="admin-input admin-textarea"
              rows={3}
              value={settings.maintenanceMessage}
              onChange={(e) => handleChange('maintenanceMessage', e.target.value)}
              disabled={!settings.maintenanceMode}
            />
          </div>
        </div>
      </div>

      {/* ==================== ACTIONS ==================== */}
      <div className="settings-actions">
        <button className="admin-btn admin-btn-ghost" onClick={handleReset}>
           Rinitialiser aux valeurs par dfaut
        </button>
        <button
          className="admin-btn admin-btn-primary"
          onClick={handleSave}
          disabled={!hasChanges || saving}
        >
          {saving ? ' Sauvegarde...' : ' Sauvegarder les modifications'}
        </button>
      </div>
    </div>
  );
};

export default SettingsAdmin;
