// ==================== TYPES EBOOKS - PHASE 20 ====================
// PedaClic : Bibliothque Ebooks Premium
// Interfaces TypeScript pour la gestion des ebooks
// =============================================================

import { Classe, Niveau } from './index';

/**
 * Catgories d'ebooks disponibles sur PedaClic
 */
export type CategorieEbook =
  | 'manuel'        // Manuels scolaires
  | 'annale'        // Annales corriges (BFEM, BAC)
  | 'guide'         // Guides de rvision / fiches rsums
  | 'litterature'   // Culture gnrale / Littrature
  | 'fiche';        // Fiches de lectures

/**
 * Labels lisibles pour chaque catgorie
 */
export const CATEGORIE_LABELS: Record<CategorieEbook, string> = {
  manuel: 'Manuels scolaires',
  annale: 'Annales corriges',
  guide: 'Guides de rvision',
  litterature: 'Culture gnrale & Littrature',
  fiche: 'Fiches de lectures'
};

/**
 * Icnes pour chaque catgorie
 */
export const CATEGORIE_ICONS: Record<CategorieEbook, string> = {
  manuel: '',
  annale: '',
  guide: '',
  litterature: '',
  fiche: ''
};

/**
 * Interface principale d'un ebook
 */
export interface Ebook {
  id: string;                       // ID unique Firestore
  titre: string;                    // Titre de l'ebook
  auteur: string;                   // Auteur(s) du document
  description: string;              // Description / rsum
  categorie: CategorieEbook;        // Catgorie d'ebook
  niveau: Niveau;                   // Niveau (collge ou lyce)
  classe: Classe | 'all';           // Classe spcifique ou toutes
  matiere?: string;                 // Matire associe (optionnel)
  couvertureURL?: string;           // URL de l'image de couverture
  fichierURL: string;               // URL du fichier PDF complet (Firebase Storage)
  aperuURL?: string;               // URL du PDF aperu (premires pages)
  nombrePages: number;              // Nombre total de pages
  pagesApercu: number;              // Nombre de pages en aperu gratuit
  tailleFichier: number;            // Taille du fichier en octets
  annee?: string;                   // Anne de publication (ex: "2024-2025")
  editeur?: string;                 // diteur du document
  isbn?: string;                    // ISBN (optionnel)
  tags?: string[];                  // Tags pour filtrage
  nombreTelechargements: number;    // Compteur de tlchargements
  nombreVues: number;               // Compteur de vues
  ordre: number;                    // Ordre d'affichage
  isActive: boolean;                // Actif ou dsactiv
  createdAt: Date;                  // Date de cration
  updatedAt?: Date;                 // Dernire mise  jour
  uploadedBy: string;               // ID admin qui a upload
}

/**
 * Donnes du formulaire d'ajout/modification d'ebook
 */
export interface EbookFormData {
  titre: string;
  auteur: string;
  description: string;
  categorie: CategorieEbook;
  niveau: Niveau;
  classe: Classe | 'all';
  matiere?: string;
  nombrePages: number;
  pagesApercu: number;
  annee?: string;
  editeur?: string;
  isbn?: string;
  tags?: string[];
  ordre: number;
  isActive: boolean;
}

/**
 * Filtres pour la bibliothque d'ebooks
 */
export interface EbookFilters {
  categorie?: CategorieEbook | 'all';
  niveau?: Niveau | 'all';
  classe?: Classe | 'all';
  matiere?: string;
  recherche?: string;
}

/**
 * Statistiques de la bibliothque (admin)
 */
export interface EbookStats {
  totalEbooks: number;
  totalTelechargements: number;
  totalVues: number;
  parCategorie: Record<CategorieEbook, number>;
  ebooksActifs: number;
}
