/**
 * ============================================
 * INSTALL PROMPT  Bannire d'installation PWA
 * ============================================
 * 
 * Affiche une bannire lgante pour installer PedaClic :
 * - Intercepte l'vnement beforeinstallprompt (Android/Chrome)
 * - Affiche des instructions manuelles pour iOS (Safari)
 * - Apparat aprs 2 visites (pas au premier chargement)
 * - Se ferme dfinitivement si l'utilisateur refuse
 * 
 * @version 1.0.0
 */

import { useState, useEffect, useCallback } from 'react';
import { Download, X, Share, Plus } from 'lucide-react';
import './InstallPrompt.css';

/* ==================== INTERFACES ==================== */

interface BeforeInstallPromptEvent extends Event {
  prompt(): Promise<void>;
  userChoice: Promise<{ outcome: 'accepted' | 'dismissed' }>;
}

/* ==================== COMPOSANT ==================== */

const InstallPrompt: React.FC = () => {
  // ===== TATS =====
  const [deferredPrompt, setDeferredPrompt] = useState<BeforeInstallPromptEvent | null>(null);
  const [showBanner, setShowBanner] = useState(false);
  const [showIOSInstructions, setShowIOSInstructions] = useState(false);
  const [isInstalling, setIsInstalling] = useState(false);

  /* ==================== DTECTION PLATEFORME ==================== */

  /**
   * Vrifie si l'appareil est iOS (iPhone/iPad)
   */
  const isIOS = (): boolean => {
    return /iPhone|iPad|iPod/.test(navigator.userAgent) && 
           !(window as any).MSStream;
  };

  /**
   * Vrifie si l'app est dj installe (mode standalone)
   */
  const isStandalone = (): boolean => {
    return window.matchMedia('(display-mode: standalone)').matches ||
           (navigator as any).standalone === true;
  };

  /* ==================== GESTION DES VISITES ==================== */

  /**
   * Incrmente le compteur de visites et retourne le total
   * La bannire s'affiche aprs 2 visites pour ne pas tre intrusive
   */
  const getVisitCount = useCallback((): number => {
    try {
      const count = parseInt(localStorage.getItem('pedaclic_visit_count') || '0', 10);
      return count;
    } catch {
      return 0;
    }
  }, []);

  const incrementVisitCount = useCallback((): void => {
    try {
      const count = getVisitCount() + 1;
      localStorage.setItem('pedaclic_visit_count', count.toString());
    } catch {
      // localStorage indisponible  on ignore silencieusement
    }
  }, [getVisitCount]);

  /**
   * Vrifie si l'utilisateur a dj ferm la bannire
   */
  const wasDismissed = (): boolean => {
    try {
      return localStorage.getItem('pedaclic_install_dismissed') === 'true';
    } catch {
      return false;
    }
  };

  /* ==================== EFFETS ==================== */

  useEffect(() => {
    // --- Ne rien faire si dj installe ou bannire refuse ---
    if (isStandalone() || wasDismissed()) return;

    // --- Incrmenter le compteur de visites ---
    incrementVisitCount();

    // --- Cas Android/Chrome : intercepter beforeinstallprompt ---
    const handleBeforeInstallPrompt = (e: Event) => {
      // Empcher Chrome d'afficher sa propre bannire
      e.preventDefault();
      // Stocker l'vnement pour l'utiliser plus tard
      setDeferredPrompt(e as BeforeInstallPromptEvent);

      // Afficher notre bannire aprs 2 visites
      if (getVisitCount() >= 2) {
        setShowBanner(true);
      }
    };

    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt);

    // --- Cas iOS : afficher les instructions manuelles ---
    if (isIOS() && getVisitCount() >= 2) {
      setShowIOSInstructions(true);
      setShowBanner(true);
    }

    // --- Dtecter quand l'app est installe ---
    const handleAppInstalled = () => {
      setShowBanner(false);
      setDeferredPrompt(null);
    };

    window.addEventListener('appinstalled', handleAppInstalled);

    // --- Nettoyage ---
    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
      window.removeEventListener('appinstalled', handleAppInstalled);
    };
  }, [getVisitCount, incrementVisitCount]);

  /* ==================== HANDLERS ==================== */

  /**
   * Lance l'installation (Android/Chrome)
   */
  const handleInstallClick = async () => {
    if (!deferredPrompt) return;

    setIsInstalling(true);

    try {
      // Afficher le prompt natif du navigateur
      await deferredPrompt.prompt();

      // Attendre la rponse de l'utilisateur
      const { outcome } = await deferredPrompt.userChoice;

      if (outcome === 'accepted') {
        setShowBanner(false);
      }
    } catch (error) {
      console.error('Erreur installation PWA:', error);
    } finally {
      setIsInstalling(false);
      setDeferredPrompt(null);
    }
  };

  /**
   * Ferme la bannire et mmorise le refus
   */
  const handleDismiss = () => {
    setShowBanner(false);
    try {
      localStorage.setItem('pedaclic_install_dismissed', 'true');
    } catch {
      // Ignore
    }
  };

  /* ==================== RENDU ==================== */

  // --- Ne rien afficher si la bannire n'est pas visible ---
  if (!showBanner) return null;

  return (
    <div className="install-prompt">
      <div className="install-prompt__container">
        
        {/* ----- Icne et texte ----- */}
        <div className="install-prompt__content">
          <div className="install-prompt__icon">
            <Download size={24} />
          </div>
          <div className="install-prompt__text">
            <h3 className="install-prompt__title">
              Installer PedaClic
            </h3>
            <p className="install-prompt__description">
              {showIOSInstructions 
                ? "Accde  tes cours mme sans connexion !"
                : "Installe l'app pour un accs rapide, mme hors-ligne !"
              }
            </p>
          </div>
        </div>

        {/* ----- Actions ----- */}
        <div className="install-prompt__actions">
          
          {/* --- Cas Android/Chrome : bouton d'installation --- */}
          {!showIOSInstructions && deferredPrompt && (
            <button 
              className="install-prompt__btn install-prompt__btn--primary"
              onClick={handleInstallClick}
              disabled={isInstalling}
            >
              {isInstalling ? 'Installation...' : 'Installer'}
            </button>
          )}

          {/* --- Cas iOS : instructions manuelles --- */}
          {showIOSInstructions && (
            <div className="install-prompt__ios">
              <p className="install-prompt__ios-text">
                Appuie sur <Share size={16} className="install-prompt__ios-icon" /> puis 
                <span className="install-prompt__ios-highlight">
                  <Plus size={14} /> Sur l'cran d'accueil
                </span>
              </p>
            </div>
          )}

          {/* --- Bouton fermer --- */}
          <button 
            className="install-prompt__btn install-prompt__btn--close"
            onClick={handleDismiss}
            aria-label="Fermer la bannire d'installation"
          >
            <X size={20} />
          </button>
        </div>
      </div>
    </div>
  );
};

export default InstallPrompt;
